import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Container from '@material-ui/core/Container';
import CircularProgress from '@material-ui/core/CircularProgress';


function DataTable({ reports }) {

    return (
      <Container>
        <TableContainer className="table-container">
          <Table  aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell style={{     
                  fontSize: '13px',
                  fontWeight: '700',
                  padding: '1.25rem .5rem .75rem',
                  color:'#6c757d'}}>
                    State
                </TableCell>

                <TableCell style={{     
                  fontSize: '13px',
                  fontWeight: '700',
                  padding: '1.25rem .5rem .75rem',
                  color:' #6c757d'}} >
                    Confirmed
                </TableCell>

                <TableCell style={{     
                  fontSize: '13px',
                  fontWeight: '700',
                  padding: '1.25rem .5rem .75rem',
                  color:' #6c757d'}} >
                    Active
                </TableCell>

                <TableCell style={{     
                  fontSize: '13px',
                  fontWeight: '700',
                  padding: '1.25rem .5rem .75rem',
                  color:' #6c757d'}} >
                    Recovered
                </TableCell>

                <TableCell style={{     
                  fontSize: '13px',
                  fontWeight: '700',
                  padding: '1.25rem .5rem .75rem',
                  color:' #6c757d'}} >
                    Deceased
                </TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              { reports.length > 0 ? reports.map((report) => (
                <TableRow key={report.state}>
                  <TableCell component="th" scope="row" style={{     
                    fontSize: '13px',
                    fontWeight: '700',
                    padding: '1.25rem .5rem .75rem',
                    color:' #6c757d'}}>{report.state}
                  </TableCell>

                  <TableCell style={{ 
                      fontSize: '14px',
                      color: '#6c757d',
                      fontWeight: 400}}>{report.confirmed}
                  </TableCell>

                  <TableCell style={{ 
                      fontSize: '14px',
                      color: '#6c757d',
                      fontWeight: 400}}>{report.active}
                  </TableCell>

                  <TableCell style={{ 
                      fontSize: '14px',
                      color: '#6c757d',
                      fontWeight: 400}}>{report.recovered}
                  </TableCell>

                  <TableCell style={{ 
                      fontSize: '14px',
                      color: '#6c757d',
                      fontWeight: 400}}>{report.deaths}
                  </TableCell>

                </TableRow>
              )) : <CircularProgress />}

            </TableBody>
          </Table>
        </TableContainer>
      </Container>

    )
}

export default DataTable;
