import React from 'react'
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

function Counter({ active }) {
    return (
        <Container className="maincontainer"  maxWidth="lg">
            <h5 className="date-time">(Last Updated) {active && active.lastupdatedtime}</h5>

            <Grid container className="counter-container">
                <Grid item md={3} sm={3} xs={6} className="counter-wrapper">
                    <h5 className="text-danger">Confirmed</h5>
                    <h4 className="text-danger">{active && active.deltaconfirmed}</h4>
                    <h1 className="text-danger">{active && active.confirmed}</h1>
                </Grid>

                <Grid item md={3} sm={3} xs={6} className="counter-wrapper">
                    <h5 className="text-primary">Active</h5>
                    <h4 className="text-primary">--</h4>
                    <h1 className="text-primary">{active && active.active} </h1>
                </Grid>

                <Grid item md={3} sm={3} xs={6} className="counter-wrapper">
                    <h5 className="text-success">Recovered</h5>
                    <h4 className="text-success">{active && active.deltarecovered}</h4>
                    <h1 className="text-success">{active && active.recovered}</h1>
                </Grid>

                <Grid item md={3} sm={3} xs={6} className="counter-wrapper">
                    <h5 className="text-dark">Deceased</h5>
                    <h4 className="text-dark">{active && active.deltadeaths}</h4>
                    <h1 className="text-dark">{active && active.deaths}</h1>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Counter
